"use client";
import { userContext } from "@/app/context/user";
import { JSX, useContext, useEffect, useState } from "react";
import { Card, CardContent } from "../ui/card";
import { ListChecksIcon, TicketCheckIcon } from "lucide-react";
import getWebcontainerState from "@/app/helpers/getWebContainerState";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "../ui/tabs";
import { Span } from "next/dist/trace";
import {
  Collapsible,
  CollapsibleContent,
  CollapsibleTrigger,
} from "@/components/ui/collapsible"
import { WebContainerType } from "@/app/types/user";

export default function WebContainerBlock({id} : {id : string}) {
  const { webContainerState, setWebContainerState } = useContext(userContext)!;
  const [ codePreview, setCodePreview ] = useState<string>("");
  const [finalArray, setFinalArray] = useState<JSX.Element>(<></>);

  function filesShowing(files : WebContainerType[]) {

    let element : JSX.Element = <></>;
        for(let i = 0; i < files.length; i++) {
          if(files[i].file) {
            element =  <span onClick={()=>(setCodePreview(files[i].content!))}>{files[i].name}</span>
           }
         else {
          const newItem = filesShowing(files[i].children!);
          <Collapsible>
                        <CollapsibleTrigger>{files[i].name}</CollapsibleTrigger>
                        <CollapsibleContent>
                        {newItem}
                        </CollapsibleContent>
            </Collapsible>
        }
      }
      
      return element;
    }
    
    
    
    useEffect(() => {
      async function getwebstate() {
        const messages = await getWebcontainerState(id);
        console.log(messages)
        setWebContainerState(JSON.parse(messages.message))
        setFinalArray(filesShowing(webContainerState!));
    }

    getwebstate()
  }, [webContainerState]);


  return (
    <div>
      <Tabs defaultValue="code">
        <TabsList>
          <TabsTrigger value="code">
            Code
          </TabsTrigger>
          <TabsTrigger value="preview">
            Preview
          </TabsTrigger>
        </TabsList>

        <TabsContent value="code">
          {/* Slider */}
          <Card>
            <CardContent>
              {finalArray ? finalArray : "No files found"}
            </CardContent>
          </Card>

          <Card>
            <CardContent>
              <span>{codePreview}</span>
            </CardContent>
          </Card>
        </TabsContent>
        <TabsContent value="preview">

        </TabsContent>
      </Tabs>
    </div>
  );
}
